.feedback-form {
     width: 480px;
   }
   
   .feedback-form > label {
     width: 100%;
     margin-bottom: 16px;
     padding: 8px;
     display: flex;
     flex-direction: column;
   }
   
   .feedback-form input,
   .feedback-form textarea {
     font: inherit;
     padding: 8px;
   }
   
   .feedback-form textarea {
     resize: none;
   }